import React from "react";

const UpvotesSection = ({ articleName, upvotes, setArticleInfo }) => {
  const upvoteActicle = async () => {
    const result = await fetch(
      `http://127.0.0.1:9000/api/articles/${articleName}/upvote`,
      {
        method: "post",
      }
    );
    const body = await result.json();
    setArticleInfo(body);
  };
  return (
    <div>
      <button onClick={() => upvoteActicle()}>Add Upvote</button>
      <p>This post has been voted {upvotes}</p>
    </div>
  );
};

export default UpvotesSection;
